<template>
  <div class="add-blog">
    <h1>添加博客</h1>
    <form v-if="!submited">
      <label for="title">博客标题</label>
      <input type="text" v-model="blog.title" required id="title" autocomplete="off">

      <label for>博客内容</label>
      <textarea v-model="blog.content" maxlength="200"></textarea>

      <div id="checkboxs">
        <label for>Vue.js</label>
        <input type="checkbox" value="Vue.js" v-model="blog.categories">

        <label for>Node.js</label>
        <input type="checkbox" value="Node.js" v-model="blog.categories">

        <label for>React.js</label>
        <input type="checkbox" value="React.js" v-model="blog.categories">

        <label for>Angular4</label>
        <input type="checkbox" value="Angular4" v-model="blog.categories">
      </div>

      <label for class="author">作者 :</label>
      <select v-model="blog.author">
        <option v-for="author in authors" :key="author">{{ author }}</option>
      </select>

      <button @click.prevent="post">添加博客</button>
    </form>

    <div v-if="submited">
      <h3>您的博客发布成功</h3>
    </div>

    <div id="preview">
      <h3>博客总览</h3>
      <p>博客标题 : {{ blog.title }}</p>
      <p>博客内容 :</p>
      <p>{{ blog.content }}</p>
      <p>博客分类</p>
      <ul v-for="category in blog.categories" :key="category">
        <li>{{ category }}</li>
      </ul>
      <p>作者 : {{ blog.author }}</p>
    </div>
  </div>
</template>

<script>
export default {
  name: "add-blog",
  data() {
    return {
      blog: {
        title: "",
        content: "",
        categories: [],
        author: ""
      },
      authors: ["Funny", "Henry", "Jack"],
      submited: false
    };
  },
  methods: {
    post() {
      //jsonplaceholder.typicode.com/posts
      // if (!this.blog.title || !this.blog.content || !this.blog.author) {
      //   console.log("为空");
      //   return;
      // }
      console.log(this.blog);
      this.$http
        .post("https://vue-test-4e132.firebaseio.com/posts.json", this.blog)
        .then(res => {
          console.log(res);
          this.submited = true;
        });
    }
  }
};
</script>

<style scoped>
input,
button,
textarea,
select {
  outline: none;
}

.add-blog,
* {
  box-sizing: border-box;
}

.add-blog {
  margin: 20px auto;
  max-width: 600px;
  padding: 20px;
}

.add-blog h1 {
  margin-bottom: 20px;
  padding-bottom: 20px;
  border-bottom: 1px solid #bbb;
}

label {
  display: block;
  margin: 20px 0 10px;
}

input[type="text"],
textarea,
select {
  display: block;
  width: 100%;
  padding: 8px;
}

textarea {
  resize: none;
  height: 120px;
}

#checkboxs {
  margin-top: 5px;
}

#checkboxs label {
  display: inline-block;
  margin-top: 0;
}

#checkboxs input {
  display: inline-block;
  margin-right: 10px;
}

button {
  display: block;
  margin: 20px 0;
  background-color: rgba(10, 131, 207, 0.8);
  color: #fff;
  border: 0;
  padding: 12px 16px;
  border-radius: 8px;
  cursor: pointer;
}

button:hover {
  background: #0a83cf;
}

#preview {
  padding: 10px 20px;
  border: 1px dotted #ccc;
  margin: 30px 0;
  margin-top: 10px;
}

ul {
  list-style-type: none;
}
</style>

