<template>
  <div class="show-blogs" v-theme="'narrow'">
    <h1>博客总览</h1>
    <div class="search">
      <input type="text" v-model="keywords" placeholder="请输入您要搜索的内容">
      <input type="button" value="search">
    </div>
    <div class="single-blog" v-for="blog in filteredBlogs" :key="blog.id">
      <router-link tag="h3" :to="'/blog/' + blog.id " v-rainbow>{{ blog.title | toUpperCase }}</router-link>
      <article>{{ blog.content | snippet }}</article>
    </div>
  </div>
</template>

<script>
export default {
  name: "show-blogs",
  data() {
    return {
      blogs: [],
      keywords: ""
    };
  },
  created() {
    // http://jsonplaceholder.typicode.com/posts
    this.$http.get("https://vue-test-4e132.firebaseio.com/posts.json").then(res => {
      return res.json()
      // this.blogs = res.body.slice(0, 10);
      // console.log(this.blogs);
    }).then(data => {
      let blogsArr = []
      for (let key in data) {
        // console.log(data[key])
        data[key].id = key
        blogsArr.push(data[key])
      }
      this.blogs = blogsArr
      console.log(this.blogs)
    })
  },
  computed: {
    filteredBlogs() {
      return this.blogs.filter(item => {
        return item.title.match(this.keywords)
      })
    }
  },
  filters: {
    toUpperCase(value) {
      return value.toUpperCase()
    }
  },
  directives: {
    'rainbow': {
      bind(el, binding, vnode) {
        el.style.color = '#' + Math.random().toString(16).slice(2, 8)
      }
    }
  }
};
</script>

<style lang="scss" scoped>
.show-blogs {
  max-width: 800px;
  margin: 0 auto;
  padding: 20px;
}

.show-blogs h1 {
  margin-bottom: 20px;
  padding-bottom: 20px;
  border-bottom: 1px solid #bbb;
}

.single-blog {
  padding: 20px;
  margin: 20px 0;
  box-sizing: border-box;
  background-color: #eee;
}

.single-blog h3 {
  margin-bottom: 12px;
  cursor: pointer;
}

.search {
  width: 100%;
  overflow: hidden;
  display: flex;
  justify-content: space-between;
}

.search input[type="text"],
.search input[type="button"] {
  box-sizing: border-box;
  border-radius: 8px;
  outline: none;
  border: none;
}

.search input[type="text"] {
  width: 80%;
  padding: 12px;
  border: 1px solid #ccc; 
}

.search input[type="button"] {
  width: 80px;
  padding: 12px 14px;
  color: #3d9fea;
  background-color: #eee;
  font-size: 18px;
  font-weight: bold;
  cursor: pointer;
}

.search input[type="button"]:hover {
  background-color: #ddd;
  color: #0e90f4;
}
</style>
