<template>
  <div class="single-blog">
    <h2>{{ blog.title }}</h2>
    <article>{{ blog.content }}</article>
    <p>作者 : {{ blog.author }}</p>
    <p>分类 : </p>
    <ul>
      <li v-for="category in blog.categories" :key="category.id">
        {{ category }}
      </li>
    </ul>
  </div>
</template>

<script>
export default {
  name: "single-blog",
  data() {
    return {
      id: this.$route.params.id,
      blog: {}
    };
  },
  created() {
    // https://vue-test-4e132.firebaseio.com/posts.json
    this.$http
      .get("https://vue-test-4e132.firebaseio.com/posts/" + this.id + ".json")
      .then(res => {
        // if (res.status === 200) {
        //   this.blog = res.body
        // }
        // console.log(this.blog)
        return res.json();
      })
      .then(data => {
        this.blog = data;
      });
  }
};
</script>

<style scoped>
.single-blog {
  max-width: 960px;
  margin: 0 auto;
  padding: 20px;
  background: #eee;
  border: 1px dotted #aaa;
}

.single-blog h2 {
  margin-bottom: 12px;
}

.single-blog article {
  margin-bottom: 12px;
}

.single-blog p {
  margin-bottom: 12px;
}

.single-blog ul {
  list-style-type: none;
  margin-left: 50px;
}
</style>
